import React, { FC, useCallback, useState } from "react";

import {
	StyledTooltip, TooltipContainer, TooltipText, TooltipWrapper,
} from "./Tooltip.styles";
import { TooltipProps } from "./Tooltip.types";


const Tooltip: FC<TooltipProps> = ({
	children,
	text,
	position = "right",
	dataQA,
	className,
	zIndex,
}) => {
	const [isVisible, setIsVisible] = useState(false);

	const handleMouseOver = useCallback(() => setIsVisible(true), []);
	const handleMouseOut = useCallback(() => setIsVisible(false), []);

	const isLong = text ? text.length > 40 : false;

	const renderedTooltip = isVisible && (
		<StyledTooltip zIndex={zIndex} position={position} isLong={isLong} data-qa={dataQA} className={className}>
			<TooltipText isLong={isLong}>{text}</TooltipText>
		</StyledTooltip>
	);

	return (
		<TooltipWrapper>
			<TooltipContainer
				aria-label={text}
				onMouseEnter={handleMouseOver}
				onMouseLeave={handleMouseOut}
				onFocus={handleMouseOver}
				onBlur={handleMouseOut}
			>
				{children}
			</TooltipContainer>
			{renderedTooltip}
		</TooltipWrapper>
	);
};

Tooltip.displayName = "Tooltip";
export default Tooltip;
